<template>
  <top-nav> 关于我的 </top-nav>

  <div class="main-box">
    <div class="top">
      <van-image
        class="top-img"
        width="50"
        height="50"
        :src="user.avatar_url"
      />
      <div class="top-text">
        <p style="margin-bottom:10px">用户名：{{user.name }}</p>
        <p>账&nbsp;&nbsp;&nbsp;号：{{ user.email }}</p>
      </div>
    </div>

    <div class="center">
      <ul class="user-list">
        <li class="van-harilinr-bottom" @click="goto('/collect')">
          <span><i class="iconfont icon-shoucang"></i>&nbsp;我的收藏</span>
          <van-icon name="arrow"></van-icon>
        </li>
        <li class="van-harilinr-bottom" @click="goto('/oeder')">
          <span><i class="iconfont icon-dingdan"></i>&nbsp;我的订单</span>
          <van-icon name="arrow"></van-icon>
        </li>
        <li class="van-harilinr-bottom" @click="goto('/setting')">
          <span><i class="iconfont icon-User"></i>&nbsp;账号管理</span>
          <van-icon name="arrow"></van-icon>
        </li>
        <li class="van-harilinr-bottom" @click="goto('/address')">
          <span><i class="iconfont icon-dizhi"></i>&nbsp;地址管理</span>
          <van-icon name="arrow"></van-icon>
        </li>
        <li class="van-harilinr-bottom" @click="goto('/about')">
          <span><i class="iconfont icon-guanyu_o"></i>&nbsp;关于我们</span>
          <van-icon name="arrow"></van-icon>
        </li>
      </ul>
    </div>
    <div class="bottom">
      <van-button round block color="#fff" @click="onlogout">
        <span style="color: #2c3e50">退出登录</span>
      </van-button>
    </div>
  </div>

  <bottom-nav></bottom-nav>
</template>

<script>
import BottomNav from "../../components/BottomNav.vue";
import TopNav from "../../components/TopNav.vue";
import { useRouter } from "vue-router";
import { reactive, onMounted, toRefs } from "vue";
import { useStore } from "vuex";
import { getUser, logout } from "../../network/regsiter";
import { Toast } from 'vant';
export default {
  name: "ProFile",
  components: { TopNav, BottomNav },
  setup() {
    const router = useRouter();
    const store = useStore();
    const state = reactive({
      user: {},
    });

    onMounted(async () => {
      await getUser().then((res) => {
        state.user = res;
      });
    });

    //退出登录
    const onlogout = () => {
        logout().then(res => {
            if(res.status == '204'){
                Toast.success("退出成功")
                store.commit("setIsLogin", false)
                window.localStorage.removeItem('token')

                setTimeout(() => {
                    router.push({path:'./home'})
                }, 500)
            }
        })
    }

    const goto = (path, query) => {
      router.push({path, query: query || {}})
    }

    return {
      ...toRefs(state),
      onlogout,
      goto
    };
  },
};
</script>

<style lang="scss" scoped>
.main-box {
  margin-top: 50px;
  .top {
    height: 100px;
    display: flex;
    width: 95%;
    background-color: #fff;
    margin-top: 70px;
    margin-left: 2.5%;
    border-radius: 10px;
    align-items: center;
    .top-img {
      margin-left: 15px;
    }
    .top-text {
      text-align: left;
      margin-left: 25px;
    }
  }
  .center {
    background-color: #fff;
    width: 95%;
    margin-left: 2.5%;
    margin-top: 30px;
    border-radius: 10px;
    .van-harilinr-bottom {
      height: 50px;
      display: flex;
      align-items: center;
      margin: 0 20px 10px 20px;
      border-bottom: 1px solid #f1f2f3;
      justify-content: space-between;
    }
    .van-harilinr-bottom:last-child {
      border-bottom: none;
    }
  }
  .bottom {
    background-color: #fff;
    width: 95%;
    margin-left: 2.5%;
    margin-top: 30px;
    border-radius: 10px;
  }
}
</style>
